import * as React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

interface JopItemProps {}

const Content = styled.div`
    width: 100%;
    height: 200px;
    display: flex;
    align-items: center;
    background-color: white;
    border-bottom: 1px solid #e6e6e6;
    & > span {
        width: 20%;
        display: inline-block;
        padding: 0 20px;
        font-size: 15px;
        &:first-child {
            color: #1e1e1e;
        }
        &:nth-child(2) {
            width: 60%;
            color: #1e1e1e;
            & > a {
                color: #333;
                text-decoration: none;
                display: block;
                font-size: 17px;
                border-bottom: 2px;
            }
        }

        &:last-child {
            border: none;
            color: #1e1e1e;
        }
    }
`;
const JopItem: React.SFC<JopItemProps> = props => {
    return (
        <Content>
            <span>카카오</span>
            <span>
                <Link to="/">카카오 전문연구요원(전직) 모집 (병특, 병역특례)</Link>
                <span>판교,제주 무관 | 정규직 | 영입종료시</span>
            </span>
            <span>기술</span>
        </Content>
    );
};

export default JopItem;



