import * as React from "react";
import styled from "styled-components";
import JopItem from "./JopItem";

export interface JopProps {}

const Wrapper = styled.div`
    width: 100%;
`;

const Header = styled.div`
    width: 100%;
    height: 40px;
    background-color: #888888;
    display: flex;
    align-items: center;

    & > span {
        width: 20%;
        display: inline-block;
        padding: 0 20px;
        background-color: #888888;
        color: white;
        font-size: 13px;
        &:after {
            content: "";
            width: 1px;
            height: 10px;
            background-color: #c3b9b9;
            display: block;
            float: right;
            margin-top: 3px;
        }

        &:nth-child(2) {
            width: 60%;
        }

        &:last-child {
            border: none;
        }
    }
`;

export default class Jop extends React.Component<JopProps, any> {
    public render() {
        return (
            <Wrapper>
                <Header>
                    <span>구분</span>
                    <span>내용</span>
                    <span>직군</span>
                </Header>
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
                <JopItem />
            </Wrapper>
        );
    }
}



